.mainWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
main {
  flex: 1;
}
@media only screen and (min-width: 320px) {
  .container {
    padding: 0 10px;
  }
}
@media only screen and (min-width: 768px) {
  .container {
    padding: 0;
  }
}
a {
  outline: none;
  color: inherit;
  text-decoration: none;
}
li {
  list-style-type: none;
}
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
address {
  font-style: normal;
}
body {
  position: relative;
  margin: 0;
  min-height: 100vh;
  padding-right: 0 !important;
  color: #000;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  font-family: 'Gotham', sans-serif;
}
section {
  overflow: hidden;
  padding: 60px 0;
}
@media only screen and (min-width: 1200px) {
  section {
    padding: 80px 0;
  }
}
@media only screen and (min-width: 1600px) {
  section {
    padding: 105px 0;
  }
}
.header {
  background: url(../images/hero-image.jpg) no-repeat center / cover;
  background-attachment: fixed;
  min-height: 840px;
  max-height: 1000px;
  height: 100vh;
  color: #fff;
}
.header__content {
  padding: 150px 0;
  margin-left: 15vh;
  width: 670px;
  text-align: center;
}
.header__text {
  padding: 20px 0;
  font-size: 42px;
  line-height: 1.4;
  font-weight: 500;
}
.header__author {
  font-size: 28px;
  font-weight: 400;
}
.header__arrow {
  padding-bottom: 60px;
}
.header__arrow a {
  display: block;
  max-width: 89px;
  margin: 0 auto;
  animation: arrow-bounce 3s infinite;
}
.headerInner {
  background: #000;
}
.headerInner img {
  margin: 0 auto;
  padding: 10px 0;
}
.about {
  position: relative;
  overflow: hidden;
}
.about__decor {
  position: absolute;
  top: 200px;
  left: 0;
  width: 1913px;
  height: 1139px;
  background: url(../images/dots.png) no-repeat center / cover;
}
.about .container {
  position: relative;
  z-index: 10;
}
.about__title {
  padding-bottom: 50px;
  text-align: center;
  font-size: 41px;
  font-weight: 700;
}
.about__subtitle {
  padding-bottom: 55px;
  text-align: center;
  font-size: 22.7px;
}
.about__text {
  padding: 0 50px;
  padding-bottom: 10px;
  font-size: 30px;
}
.about__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0 50px;
}
.about__time {
  width: 16.6%;
  margin-bottom: 5px;
  text-decoration: none;
  font-size: 22.5px;
  transition: all .3s linear;
}
.about__slider {
  margin: 25px 0;
  width: 100%;
}
.about__slide {
  display: flex;
  flex-wrap: wrap;
}
.about__link {
  width: 24%;
  margin: 5px 0.5%;
  overflow: hidden;
  max-height: 220px;
}
.about__img,
.about video {
  display: block;
  max-width: 100%;
  height: auto;
}
.about .current {
  color: #fe673a;
  font-weight: 500;
}
.about__proposal {
  padding-top: 50px;
  font-size: 22.7px;
  text-align: center;
}
.about__btn {
  display: table;
  margin: 35px auto;
  width: 300px;
  border-radius: 35px;
  background-color: #fe673a;
  box-shadow: 0px 0px 34px 0px rgba(0, 0, 0, 0.3);
  font-size: 22px;
  font-weight: 700;
  line-height: 75px;
  color: #fff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: background-color 0.3s linear;
}
.about__btn:hover {
  background-color: #fe4007;
}
.aboutInner {
  padding: 20px 0 40px 0;
}
.aboutInner .container {
  position: relative;
}
.arrBack {
  position: relative;
  z-index: 10;
}
.arrBack img {
  width: 60px;
}
.share {
  position: absolute;
  left: 20px;
  bottom: 10px;
}
.share p {
  color: #fff;
  font-weight: 700;
  padding-bottom: 15px;
}
.footer {
  padding: 15px 0;
  font-size: 18px;
  color: #fff;
  background: #0e1015;
}
.footer__box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__logo img {
  height: 70px;
}
.footer svg {
  max-width: 100%;
  margin-right: 10px;
  height: 50px;
  fill: #fff;
  transition: fill .3s linear;
}
.footer svg:hover {
  fill: #fe673a;
}
.footer__agency img {
  margin-top: 5px;
}
.remodal-first {
  padding-top: 95px;
  text-align: center;
  width: 376px;
}
@media only screen and (min-width: 320px) {
  .remodal-first {
    max-width: 376px !important;
    height: auto;
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;
    background: #f9fbfc;
    border-radius: 10px;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-first {
    padding: 50px 15px;
    max-width: 489px !important;
    height: 487px;
    text-align: left;
    border-radius: 0px;
    background: url('../images/form.png') no-repeat top center;
    padding: 120px 0 0 109px;
  }
}
@media only screen and (min-width: 320px) {
  .remodal-first .remodal-close {
    color: #000 !important;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-first .remodal-close {
    color: #fff !important;
  }
}
.remodal-first:before {
  display: none;
}
.btn-remodal {
  margin: 30px 0 0 15px;
}
.remodal-close {
  top: 12px !important;
  right: 15px !important;
  color: #fff !important;
  font-weight: 900;
}
@media only screen and (min-width: 320px) {
  .remodal-close {
    width: 10px !important;
    height: 16px !important;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-close {
    width: 35px !important;
    height: 30px !important;
  }
}
@media only screen and (min-width: 320px) {
  .remodal-close:before {
    font-size: 30px !important;
    top: -7px !important;
    left: -12px !important;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-close:before {
    font-size: 50px !important;
    top: 0px !important;
    left: 0px !important;
  }
}
.remodal-common {
  border-radius: 0;
  background: transparent;
  padding: 0;
}
@media only screen and (min-width: 320px) {
  .remodal-common {
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-common {
    padding: 40px 20px;
    max-width: 80% !important;
    text-align: center;
  }
}
.remodal-common .remodal-close {
  color: #000 !important;
}
.formBox {
  position: relative;
  background: transparent!important;
  padding: 0;
  margin: 20px auto;
  text-align: center;
}
@keyframes arrow-bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-12px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes smoothScroll {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes antiSmoothScroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-40px);
  }
}
@keyframes pulsed {
  0% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0.4); */
    transform: scale(1);
  }
  70% {
    /* box-shadow: 0 0 0 10px rgba(204,169,44, 0); */
    transform: scale(1.15);
  }
  100% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0); */
    transform: scale(1);
  }
}
@keyframes growLine {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.slick-prev,
.slick-next {
  width: 50px;
  height: 100%;
  opacity: 1;
  transition: opacity .3s linear;
  z-index: 10;
}
.slick-prev {
  top: 152px;
  left: -50px;
}
.slick-next {
  top: 213px;
  left: -50px;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .35!important;
}
.slick-dots {
  display: inline-block !important;
  left: -48px;
  top: 50%;
  transform: translateY(17%);
  border-image: transparent;
}
.slick-dots li {
  margin: 4px 8px;
  margin-bottom: 10px;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 13px;
  width: 13px;
  font-size: 0px;
  color: #fff;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
  display: none;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
